<template>
  <div class="page" style="margin-top:20px;">
    <el-form :model="searchArgs" ref="searchForm">
      <el-row>
        <el-col :span="6">
          <el-form-item label="查询范围:" prop="beginDate">
            <el-date-picker
              v-model="searchArgs.beginDateScope"
              type="daterange"
              unlink-panels
              value-format="yyyy-MM-dd"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
            ></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :span="1" :offset="1">
          <el-button type="primary" @click="query()">查询</el-button>
        </el-col>
        <el-col :span="1" :offset="14">
          <el-upload
            :show-file-list="false"
            :before-upload="beforeUpload"
            :on-success="onSuccess"
            :on-error="onError"
            :disabled="importDataDisabled"
            style="display: inline-flex;margin-right: 8px"
            action="/salary/record/import"
          >
            <el-button
              :disabled="importDataDisabled"
              type="success"
              icon="el-icon-upload2"
            >
              提交工资表
            </el-button>
          </el-upload>
        </el-col>
      </el-row>
    </el-form>
    <div style="margin-top: 10px">
      <el-table
        :data="dataSource"
        stripe
        border
        v-loading="loading"
        element-loading-text="正在加载..."
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
        style="width: 100%"
      >
        <el-table-column
          prop="id"
          align="left"
          label="id"
          width="80"
        ></el-table-column>
        <el-table-column
          prop="fileLoc"
          label="工资表名称"
          align="left"
        ></el-table-column>

        <el-table-column
          prop="createTime"
          align="left"
          label="录入时间"
        ></el-table-column>

        <el-table-column width="200" label="操作">
          <template slot-scope="scope">
            <el-button
              @click="download(scope.row)"
              style="padding: 3px"
              size="mini"
            >
              下载
            </el-button>
            <!-- <el-button style="padding: 3px" size="mini" type="primary">
              查看高级资料
            </el-button>
            <el-button
              @click="deleteEmp(scope.row)"
              style="padding: 3px"
              size="mini"
              type="danger"
            >
              删除
            </el-button> -->
          </template>
        </el-table-column>
      </el-table>
      <div style="display: flex;justify-content: flex-end;margin-top:20px;">
        <el-pagination
          background
          @current-change="currentChange"
          @size-change="sizeChange"
          layout="sizes, prev, pager, next, jumper, ->, total, slot"
          :total="total"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SalTable',
  data() {
    return {
      searchArgs: {
        beginDateScope: [],
      },
      dataSource: [],
      total: 1,
      loading: false,
      size: 10,
      importDataDisabled: false,
    }
  },
  created() {
    this.query()
  },
  methods: {
    sizeChange(currentSize) {
      this.size = currentSize
      this.query()
    },
    currentChange(currentPage) {
      this.query(currentPage)
    },
    query(page = 1) {
      const { beginDateScope, ...args } = this.searchArgs
      this.getRequest('/salary/record/', {
        ...args,
        beginDateScope:
          beginDateScope.length > 0 ? beginDateScope.join(',') : undefined,
        page: page,
        size: this.size,
      }).then((res) => {
        if (res) {
          this.dataSource = res.data
          this.total = res.total
        }
      })
    },
    onError(err, file, fileList) {
      this.importDataDisabled = false
    },
    onSuccess(response, file, fileList) {
      this.importDataDisabled = false
      this.query()
    },
    beforeUpload() {
      this.importDataDisabled = true
    },
    download(row) {
      console.log(row)
      window.open(`/salary/record/download?id=${row.id}`)
    },
  },
}
</script>

<style scoped></style>
